import { CardElement, useElements, useStripe } from "@stripe/react-stripe-js"
import axios from "axios"
import React, { useState } from 'react'
import { useHistory } from "react-router-dom";
import './style.css'
import useDadosList from "../../dados/hooks/listaHok";

const CARD_OPTIONS = {
    iconStyle: "solid",
    style: {
        base: {
            iconColor: "black",
            color: "black",
            fontWeight: 500,
            fontFamily: "Roboto, Open Sans, Segoe UI, sans-serif",
            fontSize: "12px",
            fontSmoothing: "antialiased",
            margin: 0,
            padding: 0,
            ":-webkit-autofill": { color: "blue" },
            "::placeholder": { 
                color: "#aaa",
            },
            
        },
        invalid: {
            iconColor: "#ffc7ee",
            color: "red"
        }
    }
}

export default function PaymentForm() {
    const [success, setSuccess] = useState(false)
    const stripe = useStripe()
    const elements = useElements()
    const dados = useDadosList()
 

    const history = useHistory()
    const handleSubmit = async (e) => {
        e.preventDefault()
        const { error, paymentMethod } = stripe.createPaymentMethod({
            type: "card",
            card: elements.getElement(CardElement)
        })

       
            if (!error) {
                try {
                    // const { id } = paymentMethod
                    const response = axios.post("http://localhost:4000/payment", {
                        amount: 1750,
                        // id: id
                    })
                    console.log("sucesso maluco")
    
                    if(response.data.success) {
                        history.push(`/ConfirmCompra`)
                        alert("Pagamento feito com sucesso")
                        setSuccess(true)
                        console.log("sucesso")
        
                    }
    
                } catch (e) {
                    alert("Error maluco", + e)
                    console.log(e)
                }
            } else {
                alert(error.message)
                console.log("erro maluco")
            }
       
        
        
    }

    return (
        <>
            {!success ?
                <form onSubmit={handleSubmit}>
                    <center>
                        <fieldset className="FormGroup">
                            <div className="FormRow">
                                <CardElement options={CARD_OPTIONS} />
                            </div>
                        </fieldset>
                    </center>
                    <center>
                        <button type="submit" className="buttonStrip btn btn-primary" >Pagar</button>
                    </center>
                </form>
                :
                <div>
                    <h2>You just bought a sweet spatula congrats this is the best decision of you're life</h2>
                </div>
            }

        </>
    )
}
